.clear:before,
.clear:after {
  content: " ";
  display: table;
}

.clear:after {
  clear: both;
}

.google_maps_page_gmb_settings {
  .nav-tab-wrapper {
    clear: both;
  }
  .wrap {
    margin-left: -5px;
  }

  table.form-table {
    margin: 0 0 20px;
  }

  .gmb-submit-wrap {
    clear: both;
    background: #f5f5f5;
    border-color: #dfdfdf;
    overflow: auto;
    margin: 20px 0 8px;
    padding: 12px;
    border-width: 1px;
    border-style: solid;
    font-size: 13px;
    line-height: 2.1em;
  }

  .cmb-th {
    width: 20%;
  }

  .cmb-td {
    width: 80%;
  }

}

/*---------------------------
Welcome Header
-----------------------------*/
.welcome-header {
  margin-bottom: 20px;

  .main-heading {
    font-size: 36px;
    font-weight: 400;
    line-height: 1.1em;
    margin: 10px 0 20px;
  }

  .main-subheading {
    font-size: 20px;
    line-height: 1.4em;
    margin: 0;
  }

  .logo-svg {
    margin: 15px 0 0;
    svg {
      height: 185px;
    }
  }

  .logo-svg.logo-svg-small {
    float: right;
    margin: 0;
  }

  .new-window {
    background: url('../img/new-window-icon.png') no-repeat right 5px transparent;
    padding-right: 13px;
    margin-right: 4px;
  }

}

/*------------------------------------------
Welcome Closed heading
--------------------------------------------*/
.gmb-plugin-heading {
  display: none;
  + #Layer_1 {
    width: auto;
    height: 37px;
    margin: 0 10px 0 5px;
  }
}

a.button.gmb-orange-btn, .gmb-orange-btn {
  @include orange-button;
}

#wpwrap .gmb-settings-header-btn {
  margin: 5px 0 0;
}

/*------------------------------------------
Social Media
--------------------------------------------*/
.social-items-wrap {
  float: left;
  > div {
    display: inline-block;
  }

  .fb-item-wrap {
    width: 100px;
  }
  .twitter-item-wrap {
    width: 145px;
  }
  .google-plus {
    margin-left: 20px;
    width: 200px;
  }

  .wordimpress-logo {
    position: relative;
    top: 10px;
    left: -17px;
    a {
      display: block;
    }
    .logo-link {
      background: url('../img/wordimpress-logo-small.png') no-repeat 0 0 transparent;
      width: 100px;
      height: 40px;
    }
  }

  .gmb-settings-header-btn {
    margin-top: -6px !important;
  }

}

.hide-welcome {
  margin: 21px 0 0 30px;
  display: inline-block;

  span {
    background: url('../img/arrows.png') no-repeat 0 -33px rgba(0, 0, 0, 0);
    width: 15px;
    height: 14px;
    display: inline-block;
    margin-left: 3px;
  }
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.small-desc {
  font-size: 12px;
  font-style: italic;
  color: #B3B3B3;
}

/*---------------------------
Map Options Tab
-----------------------------*/

.size-label {
  margin: 8px 10px 0 0;
  font-style: italic;
  width: 50px;
}

.width_unit_label, .width_radio, .map-width, .map-height, .size-label, .radio-left, .label-left {
  float: left !important;
}

/*---------------------------
 Map Size
 ----------------------------*/
.size-labels-wrap {
  margin: 8px 0 0 10px;
  padding: 0;
  float: left;

  input[type="radio"] {
    margin-right: 0;
    float: left;
  }
  label {
    margin: 0 10px 0 0;
    float: left;
  }

}

.map-width, .map-height {
  width: 55px !important
}

/*---------------------------
Lat/Lng
 ----------------------------*/
#geolocate-wrap {
  margin: 0 0 5px;
}

label.geocode-label {
  width: 130px;
  margin-right: 0;
}

label.yes-label {
  margin-right: 10px;
}

.lat-lng-wrap {
  margin: 10px 20px 10px 0;
  clear: none;

  > span, > input {
    float: left;
  }
  > span {
    margin: 8px 5px 0 0;
    width: 80px;
    font-style: italic;
    font-weight: bold;
  }

  input[type=text].latitude, input[type=text].longitude {
    width: 120px;
    float: left;
  }
}

/*---------------------------
General Options Tab
-----------------------------*/
#gmb_menu_position {
  width: 40px;
}

/*---------------------------
System Settings Tab
-----------------------------*/
#system-info-textarea {
  width: 100%;
  height: 600px;
}